.hero-summary-details {
    display: flex;
    justify-content: space-around;
    margin-top: 4px;
    margin-bottom: 4px;
}

.hero-summary-details .hero-rarity {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.hero-summary-details .hero-level {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.hero-summary-details .primary {
    font-size: 1em;
}

.hero-summary-details .secondary {
    font-size: 0.8em;
}
