.hero-lookup .instructions {
    margin: 10px 0 15px 0;
    font-size: 0.9em;
    width: 550px;
}

.manual-selecter {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.origin-realm-label .MuiFormControlLabel-label {
    font-size: 0.9em;
}

.origin-realm-selecter {
    flex-direction: row !important;
}

.origin-realm-selecter .origin-realm-radio {
    padding: 5px;
}

.origin-realm-selecter .crystal-icon, 
.origin-realm-selecter .jade-icon, 
.origin-realm-selecter .jewel-icon {
    width: 20px;
    height: 20px;
    margin: 0 0 0 0;
}

.wallets-selecter {
    display: flex;
}

.wallets-form {
    width: 100%;
}

.connect-my-accounts {
    display: flex;
    justify-content: center;
}

.hero-lookup-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 550px;
}

.hero-lookup-item {
    margin: 4px;
}

.hero-lookup-item:hover {
    filter: drop-shadow(3px 3px 3px gray);
}
