.about-section .icon {
    width: 50px;
    height: 50px;
}

.about-details {
    font-size: 0.9em;
    border: 1px solid rgba(25, 118, 210, 1);
    border-radius: 4px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400;
    background-color: white;
    box-shadow: 24;
}

.about-details .title {
    font-weight: bold;
    color: rgba(25, 118, 210, 1);
    border-bottom: 1px solid rgba(25, 118, 210, 1);
    background-color: rgba(25, 118, 210, 0.5);
    padding: 3px 6px;
}

.about-details .content {
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-details .content > div {
    margin-top: 5px;
    margin-bottom: 5px;
}

.credits-section {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.credit {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.credit > div {
    margin: 2px 0;
}

.credit .created-by-text {
    font-size: smaller;
}

.discord-link {
    display: flex;
    justify-content: center;
}

.discord-link a {
    color:#d32f2f;
    font-weight: bold;
}

.discord-user {
    display: flex;
}

.discord-user img {
    margin: 0 10px;
    width: 25px;
    height: 25px;
}

.discord-username, .tool-name {
    display: inline;
    font-style: italic;
    font-weight: bold;
}