.account-card {
    display: flex;
    margin: 5px;
}

.account-card .avatar img {
    border-radius: 9999px;
}

.account-card .details {
    margin-left: 10px;
}

.account-card .details .account-name {
    color: #334155;
    font-weight: 500;
    font-size: 0.9em;
    line-height: 1.5em;
}

.account-card .details .address {
    color: #94a3b8;
    font-size: 0.75em;
    line-height: 1em;
    color: gray;
}
