.logo-section {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo-section .summoning-section {
    font-family: 'Marcellus', serif;
    font-size: 28pt;
    font-variant-caps: all-small-caps;
    letter-spacing: 0.25em;
    margin-left: 10px;
    color: rgba(0, 0, 0, 0.5);
}

.logo-section .icon {
    margin: 0 20px;
    width: 50px;
    height: 50px;
}

.logo-section .match-maker-section {
    display: flex;
    align-items: baseline;
}

.logo-section .match-maker-section .text {
    font-family: 'Sacramento', cursive;
    font-size: 32pt;
}
